export default function CreateRecurringBillingModel() {
  const { t } = useTranslation();
  let { state, search, pathname } = useLocation();
  const history = useHistory();
  const selectNavToggle = useContext(NavToggle);

  const query = new URLSearchParams(search);
  let billingModelId = query.get("billing-model-id");
  let billingModelTypeFromQuery = query.get("billing-model-type");

  const isEdit = () => {
    return pathname.includes("edit-billing-model");
  };

  const trialTypeForEdit = () => {
    switch (BillingModelTypes[state?.["billingModelType"]]) {
      case String(BillingModelTypes.RecurringPaidTrial): {
        return BMTrialTypes.PaidTrial;
      }
      case String(BillingModelTypes.RecurringFreeTrial): {
        return BMTrialTypes.FreeTrial;
      }
      case String(BillingModelTypes.Recurring):
      case String(BillingModelTypes.RecurringDynamic):
      default: {
        return BMTrialTypes.NoTrial;
      }
    }
  };
  const checkBillingModelType = () => {
    if (isDynamic) {
      return BillingModelTypes.RecurringDynamic;
    }
    switch (trialType) {
      case BMTrialTypes.NoTrial: {
        return BillingModelTypes.Recurring;
      }
      case BMTrialTypes.FreeTrial: {
        return BillingModelTypes.RecurringFreeTrial;
      }
      case BMTrialTypes.PaidTrial: {
        return BillingModelTypes.RecurringPaidTrial;
      }
    }
  };

  const supportedTokens = useSupportedTokens();
  const billingModelType =
    billingModelTypeFromQuery || state?.["billingModelType"];

  const [isDynamic, setIsDynamic] = useState(
    state?.["isDynamic"] ? state?.["isDynamic"] : false
  );
  const [currency, setCurrency] = useState(
    state?.["token"]
      ? supportedTokens[state["token"]]
      : {
          address: "0xc5d495EEaA84942095b769342bC71125721692Da",
          symbol: "PMA",
          name: "PumaPay",
          decimals: 18,
        }
  );

  const [merchantName, setMerchantName] = useState<string>("");
  const [merchantUrl, setMerchantUrl] = useState<string>("");
  const [reference, setReference] = useState<string>("");
  const [title, setTitle] = useState("");
  const [price, setPrice] = useState("");

  const [bmFrequency, setbmFrequency] = useState(1);
  const [bmFrequencyPeriod, setbmFrequencyPeriod] =
    useState(DEFAULT_PERIOD_VALUE);

  const [noOfDurationPeriods, setForNoOfDurationPeriods] = useState(1);
  const [durationPeriod, setDurationPeriod] = useState(DEFAULT_PERIOD_VALUE);

  // const noOfDurationPeriodsForEdit: IDuration = getDuration(
  //   Number(state?.["frequency"]) * Number(state?.["numberOfPayments"])
  // );
  //
  // const [durationPeriod, setDurationPeriod] = useState({
  //   label: isEdit()
  //     ? noOfDurationPeriodsForEdit.label
  //     : state?.["durationPeriod"]
  //     ? state?.["durationPeriod"].label
  //     : DEFAULT_PERIOD_VALUE.label,
  //   periodInSeconds: isEdit()
  //     ? noOfDurationPeriodsForEdit.periodInSeconds
  //     : state?.["durationPeriod"]
  //     ? state?.["durationPeriod"].periodInSeconds
  //     : DEFAULT_PERIOD_VALUE.periodInSeconds,
  // });

  const [trialType, setTrialType] = useState<Number>(BMTrialTypes.NoTrial);
  const [numberOfTrialPeriods, setEveryTrailPeriod] = useState<Number>(1);
  const [trialPeriod, setTrialPeriod] = useState(DEFAULT_PERIOD_VALUE);
  const [initialAmount, setInitialAmount] = useState("");

  const onFrequencyChanged = (e) =>
    setbmFrequencyPeriod(
      supportedLifecycles.find(
        (cycle) => Number(cycle.periodInSeconds) === Number(e.target.value)
      )
    );
  const onNumberOfPaymentChanged = (e) =>
    setDurationPeriod(
      supportedLifecycles.find(
        (cycle) => Number(cycle.periodInSeconds) === Number(e.target.value)
      )
    );
  const onChangeTrailPeriod = (e) =>
    setTrialPeriod(
      supportedLifecycles.find(
        (cycle) => Number(cycle.periodInSeconds) === Number(e.target.value)
      )
    );

  const frequencyOptions = supportedLifecycles.map((freq) => {
    if (freq.env.includes(process.env.REACT_APP_ENV)) {
      return (
        <option key={freq.periodInSeconds} value={freq.periodInSeconds}>
          {freq.label}
        </option>
      );
    }
  });
  const trailPeriodOptions = supportedLifecycles.map((freq) => (
    <option key={freq.periodInSeconds} value={freq.periodInSeconds}>
      {freq.label}
    </option>
  ));
  const currencyOptions = Object.keys(supportedTokens).map((index) => {
    return (
      <option
        key={supportedTokens[index].address}
        value={supportedTokens[index].address}
      >
        {supportedTokens[index].symbol}
      </option>
    );
  });

  const goToPreviousPage = () => {
    history.goBack();
  };

  const handleNextButtonClick = (e) => {
    e.preventDefault();
    history.push({
      pathname: isEdit()
        ? `/merchant/recurring-billing/edit-billing-model/widget-preview`
        : `/merchant/create/widget-preview`,
      state: {
        billingModelId: billingModelId ? billingModelId : null,
        checked: isDynamic,
        merchantName: merchantName,
        merchantUrl: merchantUrl,
        reference: reference,
        title,
        price: toTokenDecimals(price, currency.decimals.toString()),
        currency,
        bmFrequency,
        noOfDurationPeriods,
        bmFrequencyPeriod,
        durationPeriod,
        trialPeriod,
        numberOfTrialPeriods,
        isEdit: isEdit(),
        initialAmount: initialAmount
          ? toTokenDecimals(
              initialAmount.toString(),
              currency.decimals.toString()
            ).toString()
          : null,
        trialType,
        billingModelType: checkBillingModelType(),
        onUpdate: !!billingModelId,
      },
    });
  };

  const backUrl = () => {
    return isEdit()
      ? `/merchant/billing-model-details?billing-model-type=${billingModelType}&billing-model-id=${billingModelId}`
      : "/merchant/create/choose-billing-model-type";
  };

  useEffect(() => {
    if (state) {
      setMerchantName(state["merchantName"]);
      setMerchantUrl(state["merchantUrl"]);
      setReference(state["uniqueReference"]);
      setTitle(state["name"]);
      setPrice(
        fromTokenDecimals(
          state["amount"],
          currency.decimals.toString()
        ).toString()
      );

      // Billing Cycle
      const bmFrequencyForEdit: IDuration = getDuration(
        Number(state["frequency"])
      );
      setbmFrequency(
        isEdit() ? bmFrequencyForEdit.value : Number(state["bmFrequency"])
      );

      setbmFrequencyPeriod({
        label: isEdit()
          ? bmFrequencyForEdit.label
          : state["bmFrequencyPeriod"]?.label,
        periodInSeconds: isEdit()
          ? bmFrequencyForEdit.periodInSeconds
          : state["bmFrequencyPeriod"]?.periodInSeconds,
      });
      // console.log(isEdit(), state, bmFrequencyForEdit);

      // Duration
      const noOfDurationPeriodsForEdit: IDuration = getDuration(
        Number(state["frequency"]) * Number(state["numberOfPayments"])
      );
      setForNoOfDurationPeriods(
        isEdit()
          ? noOfDurationPeriodsForEdit.value
          : Number(state["noOfDurationPeriods"])
      );
      setDurationPeriod({
        label: isEdit()
          ? noOfDurationPeriodsForEdit.label
          : state["durationPeriod"]?.label,
        periodInSeconds: isEdit()
          ? noOfDurationPeriodsForEdit.periodInSeconds
          : state["durationPeriod"]?.periodInSeconds,
      });

      // Trial details
      const trialPeriodForEdit: IDuration = getDuration(state["trialPeriod"]);
      setTrialType(isEdit() ? trialTypeForEdit() : state["trialType"]);
      setEveryTrailPeriod(
        isEdit()
          ? trialPeriodForEdit.value
          : Number(state["numberOfTrialPeriods"])
      );
      setTrialPeriod({
        label: isEdit()
          ? trialPeriodForEdit.label
          : state["trialPeriod"]?.label,
        periodInSeconds: isEdit()
          ? trialPeriodForEdit.periodInSeconds // no period in seconds attr
          : state["trialPeriod"]?.periodInSeconds,
      });
      setInitialAmount(
        fromTokenDecimals(
          state["initialAmount"],
          currency.decimals.toString()
        ).toString()
      );
    }
  }, [state]);

  return (
    <>
      <MainContent
        className={
          selectNavToggle ? "recurring-billing open" : "recurring-billing"
        }
      >
        <ProgressBar />
        <div className="arrow">
          <StyledLink className="active" as={Link} to={backUrl()}>
            {" "}
            <img src={ArrowRight} alt="arrow" />{" "}
          </StyledLink>
        </div>
        <Box
          as="form"
          style={{ width: "80%", overflowY: "scroll" }}
          onSubmit={(e) => handleNextButtonClick(e)}
        >
          <h3 id="Boxh3Tag">{t("recurringBillingModelName")}</h3>
          <p id="BoxPTag">{t("setModelDescriptionRecurringBillingModel")}</p>
          {!billingModelId && (
            <SwitchWrapper>
              <div className="small-switch" style={{ alignItems: "center" }}>
                <Switch
                  checked={isDynamic}
                  setChecked={() => {
                    setIsDynamic(!isDynamic);
                  }}
                />
                <p>{t("dynamicBillingModelName")}</p>
                <MouseoverTooltip
                  text="Setting a billing model to be dynamic will allow you to set the currency and price programmatically on the fly."
                  children={
                    <img
                      style={{ paddingLeft: "7px", display: "flex" }}
                      src={questionIcon}
                      alt="question"
                    />
                  }
                />
              </div>
            </SwitchWrapper>
          )}

          <InputController className="input-wrapper">
            <AsideDiv>
              <Wrapper>
                <div className="billing-wrap" id="IMerchantName">
                  <label>{t("merchantName")}</label>
                  <Input
                    id="bm-merchant-name"
                    name="bm-merchant-name"
                    type="text"
                    value={merchantName}
                    onChange={(e) => setMerchantName(e.target.value)}
                    placeholder={t("bmMerchantNamePlaceHolder")}
                  />
                  <p className="msg">
                    Merchant name will be shown on the blockchain transaction
                    details
                  </p>
                </div>

                <div className="billing-wrap" id="IMerchantUrl">
                  <label>{t("merchantUrl")}</label>
                  <Input
                    id="bm-merchant-url"
                    name="bm-merchant-url"
                    type="text"
                    value={merchantUrl}
                    onChange={(e) => setMerchantUrl(e.target.value)}
                    placeholder={t("bmMerchantUrlPlaceHolder")}
                  />
                  <p className="msg">
                    Merchant url will be shown on the blockchain transaction
                    details
                  </p>
                </div>
              </Wrapper>

              <Wrapper>
                <div className="billing-wrap" id="IUniqueReference">
                  <label>{t("uniqueReference")}</label>
                  <Input
                    id="bm-unique-reference"
                    name="bm-unique-reference"
                    type="text"
                    value={reference}
                    onChange={(e) => setReference(e.target.value)}
                    placeholder={t("bmUniqueReferencePlaceHolder")}
                    disabled={isEdit() ? "disabled" : ""}
                  />
                  <p className="msg">
                    Unique reference will be shown on the blockchain transaction
                    details
                  </p>
                </div>
                <div className="billing-wrap" id="IBillingModelName">
                  <label>{t("billingModelName")}</label>
                  <Input
                    className="custom-field"
                    id="bm-billingModel-name"
                    name="bm-billingModel-name"
                    type="text"
                    value={title}
                    onChange={(e) => setTitle(e.target.value)}
                    placeholder={t("billingModelNamePlaceholder")}
                    disabled={isDynamic ? "disabled" : ""}
                  />
                  <p className="msg">{t("billingModelNameDescription")}</p>
                </div>
              </Wrapper>
              <Wrapper>
                <div className="billing-wrap" id="IBillingModelName">
                  <label>
                    {t("currency")}, {t("price")}
                  </label>
                  <div className="multi-controller">
                    <Select
                      id="bm-currency"
                      name="bm-currency"
                      defaultValue={currency?.address}
                      onChange={(e) => {
                        setCurrency(supportedTokens[e.target.value]);
                      }}
                      disabled={isDynamic || billingModelId ? "disabled" : ""}
                    >
                      {currencyOptions}
                    </Select>
                    <Input
                      className="custom-field"
                      id="bm-price"
                      name="bm-price"
                      type="number"
                      placeholder="0.00"
                      step="0.000000000000000001"
                      value={price}
                      onChange={(e) => setPrice(e.target.value)}
                      disabled={isDynamic || billingModelId ? "disabled" : ""}
                      min="0"
                    />
                  </div>
                </div>
              </Wrapper>
              <Wrapper>
                <div className="bottom-wrap">
                  <h3 className="heading">{t("billingCycle")}</h3>
                  <p className="info">{t("billingCycleDescription")}</p>
                  <div className="calender">
                    <div className="custom-input">
                      <span className="position-absolute">Every </span>
                      <Input
                        className="custom-field"
                        id="bm-cycle-periods"
                        name="bm-cycle-periods"
                        type="number"
                        placeholder="no. of periods"
                        value={bmFrequency}
                        onChange={(e) => setbmFrequency(e.target.value)}
                        disabled={
                          isDynamic || billingModelId || billingModelId
                            ? "disabled"
                            : ""
                        }
                        min="1"
                      />
                    </div>
                    <div className="custom-frequency">
                      <Select
                        id="bm-frequency"
                        name="bm-frequency"
                        value={bmFrequencyPeriod.periodInSeconds}
                        onChange={onFrequencyChanged}
                        disabled={isDynamic || billingModelId ? "disabled" : ""}
                      >
                        {frequencyOptions}
                      </Select>
                    </div>
                  </div>
                </div>

                {/* SETTLE IN other currency */}
                <div className="msg" />
                <div className="bottom-wrap">
                  <h3 className="heading">{t("duration")}</h3>
                  <p className="info">{t("durationDescription")}</p>
                  <div className="calender">
                    <div className="custom-input">
                      <span className="position-absolute">For </span>
                      <Input
                        disabled={isDynamic || billingModelId ? "disabled" : ""}
                        id="number-of-payment-count"
                        name="number-of-payment-count"
                        value={noOfDurationPeriods}
                        onChange={(e) =>
                          setForNoOfDurationPeriods(e.target.value)
                        }
                        type="number"
                        min="1"
                      />
                    </div>
                    <div>
                      <Select
                        id="bm-payment-count"
                        name="bm-payment-count"
                        value={durationPeriod.periodInSeconds}
                        onChange={onNumberOfPaymentChanged}
                        disabled={isDynamic || billingModelId ? "disabled" : ""}
                      >
                        {frequencyOptions}
                      </Select>
                    </div>
                  </div>
                </div>
              </Wrapper>
            </AsideDiv>
            <AsideDiv className="AsideDivTwo" />
          </InputController>
          <TrailWrap className="trailWrap">
            <h3 style={{ display: "flex" }}>
              {t("trialPeriod")}
              {isDynamic && (
                <MouseoverTooltip
                  text="Note that you need to specify the Trial Type of your dynamic billing model."
                  children={
                    <img
                      style={{ paddingLeft: "7px" }}
                      src={questionIcon}
                      alt="question"
                    />
                  }
                />
              )}
            </h3>

            <p>{t("trialPeriodDescription")}</p>
            <div className={`stepWrap ${isEdit() ? "edit" : ""}`}>
              <div
                className={`steps ${
                  trialType === BMTrialTypes.NoTrial ? "selected" : ""
                }`}
              >
                <input
                  type="radio"
                  readOnly
                  name="pname"
                  onClick={() => setTrialType(BMTrialTypes.NoTrial)}
                  disabled={!!billingModelId}
                  checked={trialType === BMTrialTypes.NoTrial}
                />
                <div className="borderSelect" />
                <div className="select-with-radio">
                  <span className="radioShape" />
                  <p>{t("noTrial")}</p>
                </div>
              </div>
              <div
                className={`steps ${
                  trialType === BMTrialTypes.FreeTrial ? "selected" : ""
                }`}
              >
                <input
                  type="radio"
                  readOnly
                  disabled={!!billingModelId}
                  name="pname"
                  onClick={() => setTrialType(BMTrialTypes.FreeTrial)}
                  checked={trialType === BMTrialTypes.FreeTrial}
                />
                <div className="borderSelect" />
                <div className="select-with-radio">
                  <span className="radioShape" />
                  <p>{t("freeTrial")}</p>
                </div>
              </div>
              <div
                className={`steps ${
                  trialType === BMTrialTypes.PaidTrial ? "selected" : ""
                }`}
              >
                <input
                  type="radio"
                  readOnly
                  disabled={!!billingModelId}
                  name="pname"
                  onClick={() => setTrialType(BMTrialTypes.PaidTrial)}
                  checked={trialType === BMTrialTypes.PaidTrial}
                />
                <div className="borderSelect" />
                <div className="select-with-radio">
                  <span className="radioShape" />
                  <p>{t("paidTrial")}</p>
                </div>
              </div>
            </div>
            {trialType !== BMTrialTypes.NoTrial && (
              <div className="select-controller">
                <div className="billing-wrap">
                  <h3>{t("periodDurationUnderTrial")}</h3>
                  <div className="input-fields">
                    <Input
                      id="trailTwoInput"
                      type="number"
                      min="1"
                      value={numberOfTrialPeriods}
                      onChange={(e) => setEveryTrailPeriod(e.target.value)}
                      disabled={isDynamic || !!billingModelId ? "disabled" : ""}
                    />
                    <Select
                      value={trialPeriod.periodInSeconds}
                      onChange={onChangeTrailPeriod}
                      name="bm-trail-period-duration"
                      id="bm-trail-period-duration"
                      disabled={isDynamic || !!billingModelId ? "disabled" : ""}
                    >
                      {trailPeriodOptions}
                    </Select>
                  </div>
                </div>
                {trialType === BMTrialTypes.PaidTrial && (
                  <div className="billing-wrap">
                    <h3 id="paidPrice">Price</h3>
                    <div className="input-fields">
                      <Input
                        min="0"
                        className="without-arrow"
                        type="number"
                        placeholder="0.00"
                        step="0.000000000000000001"
                        value={initialAmount}
                        onChange={(e) => setInitialAmount(e.target.value)}
                        disabled={
                          isDynamic || !!billingModelId ? "disabled" : ""
                        }
                      />
                      <span
                        style={{
                          lineHeight: "2rem",
                          marginLeft: "1rem",
                        }}
                      >
                        {currency.symbol}
                      </span>
                    </div>
                  </div>
                )}
              </div>
            )}
          </TrailWrap>
          <BtnWrapper className="btn-wrap">
            <ButtonPrimary
              id="ButtonPrimaryRecurring"
              onClick={goToPreviousPage}
            >{`Cancel`}</ButtonPrimary>
            <ButtonPrimary id="create-bm-button">
              {t("createBillingModel")}
            </ButtonPrimary>
          </BtnWrapper>
        </Box>
      </MainContent>
    </>
  );
}
